.game-ui-container{
    height: 76vh;
    width: 38vh;
    position: absolute;
    pointer-events: none;
    z-index: 100;
    background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(99,0,213,0) 25%, rgba(159,99,229,0) 75%, rgba(255,255,255,1) 100%);
}
.game-ui-container.mobile{
    height: 100%;
    width: 100vw;
    position: absolute;
    pointer-events: none;
    /*background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(99,0,213,0) 25%, rgba(159,99,229,0) 75%, rgba(255,255,255,1) 100%);*/
}
.offer-tabs{
    pointer-events: auto;
    position: absolute;
    /*bottom: 92%;*/
    top:1px;
    left: 20%;
    background-color: #ffffff;
    width: 60%;
    height: 8%;
    /*transform: translateY(-1%);*/
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom-left-radius: 1.7vh;
    border-bottom-right-radius: 1.7vh;
}
.offer-tabs.mobile{
    height: 4em;
    margin-top: 50vh;
    z-index: 500;
    position: sticky;
    border-radius: 1.7vh;
}

.offer-tabs>div{
    flex: 1;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    text-align: center;
    /*font-size: 100;*/
    font-weight: 700;
    border-right: 1px solid rgba(0, 0, 0, 0.13);
}
.offer-tabs>div:last-child{
    border-right: none;
}
.game-ui-container .offer-tabs svg{
    padding-top: 10%;
}
.game-ui-container.mobile .offer-tabs svg{
    padding-top: 0;
}
.offer-tabs>div.selected{
    position: relative;
}
.offer-tabs>div.selected,.offer-tabs>div.selected>svg{
    color:#00ff7e;
    fill:#00ff7e;
}
.offer-tabs>div.selected::after{
    content: '';
    position: absolute;
    bottom: 0;
    width: 70%;
    height: 10%;
    border-radius: 100px;
    background-color: #00ff7e;
}
.map-btn{
    cursor: pointer;
}
.map-btn-container .map-btn{
    transform: scale(0.8);
}
.map-btn-container:active .map-btn{
    transition: transform 400ms ease;
    transform: scale(0.9);
}
.text-bebas-neue{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
}
.text-bebas-neue>div{
    font-family: 'BebasNeue', sans-serif;
}
.flex-center{
    display: flex;
    align-items: center;
    justify-content: center;
}
.flex-center-column{
    display: flex;
    flex:1;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

