*{
    -webkit-tap-highlight-color: transparent;
}
.app-scroll-to-game {
    transition: transform 500ms ease;
    transform: translateY(-100vh);
}

.app-scroll-to-heading {
    transition: transform 500ms ease;
    transform: translateY(0);
}

.app-scroll-to-footer {
    transition: transform 500ms ease;
    transform: translateY(-200vh);
}


.yooz-text-purple {
    color: #6300d5;
}

.yooz-bg-purple {
    background-color: #6300d5;
}

.yooz-offers-heading {
    height: 100vh;
    overflow-y: hidden;
    background: linear-gradient(180deg, rgba(99, 0, 213, 1) 80%, rgba(255, 255, 255, 1) 100%);
    display: flex;
    align-items: center;
    justify-content: center;
}

.yooz-offers-heading.mobile {
    background: rgba(99, 0, 213, 1);
    height: 40vh;
    display: block;
}

.arrow-button-heading {
    padding: 0;
    margin: 0;
    user-select: none;
    height: 50px;
    width: 50px;
    background-color: transparent;
    border: 0;
    cursor: pointer;
    transition: transform 150ms ease;
    -webkit-tap-highlight-color: transparent;
}

.arrow-button-heading.purple {
    filter: brightness(50%) sepia(100) saturate(100) hue-rotate(270deg);
}
.arrow-button-heading.small {
    height: 35px;
    width: 35px;
}
.arrow-button-heading:hover {
    transform: scale(1.2);
}

.yooz-offers-footer {
    height: 100vh;
    padding-top: 10vh;
    overflow-y: hidden;
    position: relative;
    margin-top: 10vw;
}
.yooz-offers-footer.mobile{
    height: 110vh;
    overflow-y: visible;
}
.yooz-offers-footer .footer-shape {
    width: 100%;
    height: 20vh;
    background-color: #009afc;
    position: absolute;
    bottom: 0;
}

.yooz-offers-footer .footer-shape img {
    width: 100%;
    position: absolute;
    bottom: 100%;
}
.footer-download-yooz{
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    margin: 20px;
}
.footer-download-yooz > div{
    display: flex;
    justify-content: center;

}
.footer-download-yooz > div >div{
    width: 50%;
}
.footer-download-yooz > div >div> a img{
    height: 100%;
    width: calc(100% - 10px);
    margin: 0 5px;
    object-fit: contain;
}
/*=======================*/
.yooz-offers-scroll-game {
    /*overflow: hidden;*/
    height: 100vh;
}

.yooz-offers-scroll-game.mobile {
    height: auto;
    background-color: black;
}

.scroll-game {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10% 40px 0;
    justify-content: flex-start;
    /*background-color: white;*/
    background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 30%, rgba(255,255,255,0) 100%);
   position: relative;
}

.scroll-game .map-bg {
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
   top: 0;
    object-fit: contain;
}

.yooz-offers-scroll-game h1, .scroll-game h1 {
    text-align: center;
    z-index: 1;
    font-family: 'Ooredoo-Heavy', sans-serif;
    font-size: 2rem;
}

.yooz-offers-scroll-game h3, .scroll-game h3 {
    text-align: center;
    z-index: 1;
    font-family: 'Avenir-Book', sans-serif;
    font-size: 1.2rem;
}

/*=======================================*/
.yooz-header-banner {
    display: flex;
    max-width: 80%;
    margin: auto;
    padding: 30px 60px 10vh;
}

.yooz-header-banner .unlimited {
    flex-basis: 35%;
    padding: 16px;
}

.yooz-header-banner .unlimited img {
    min-width: 100%
}

.yooz-header-banner .offers {
    flex-basis: 65%;
}

.yooz-header-banner .offers .fox img {
    width: 100%;
}

.yooz-header-banner .offers .downloads {
    width: 100%;
    margin: auto;
    padding-top: 50px;
    padding-left: 10%;
}

.yooz-header-banner .offers .downloads img {
    margin: 10px;
    width: 32%;
}

.yooz-header-banner .offers .downloads img:hover {
    cursor: pointer;
    transition: filter 300ms ease;
    filter: brightness(1.2)
}


/*=======================================*/
.yooz-header-banner-mobile {
    display: flex;
    height: 40vh;
    /*margin-left: 10vw;*/
    /*margin-right: 10vw;*/
    justify-content: space-around;
}
.yooz-header-banner-mobile .unlimited, .yooz-header-banner-mobile .offers{
    height: 100%;
    width: 50%;
}
.yooz-header-banner-mobile .offers img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: bottom;
}
.yooz-header-banner-mobile .unlimited > img{
    width: 100%;
    height: calc( 80% - 16px);
    padding: 8px;
    object-fit: contain;
    object-position: bottom;
}
.yooz-header-banner-mobile .unlimited .downloads{
    width: 120%;
    height: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.yooz-header-banner-mobile .unlimited .downloads a{
    margin: 8px;
    height: 100%;
    width: 50%;
}
.yooz-header-banner-mobile .unlimited .downloads img:hover{
        cursor: pointer;
        transition: filter 300ms ease;
        filter: brightness(1.2)
}
.yooz-header-banner-mobile .unlimited .downloads img{
    height: 100%;
    width: 100%;
    object-fit: contain;
}
.yoozmob-header {
    background-color: white;
    width: 100%;
    height: 100%;
    margin-bottom: 40px;
    min-height: 40vw;
}
.yoozmob-carousel-item {
    width: 100%;
    height: 100vh;
    background-color: white;
    min-height: 40vw;
}

.yoozmob-carousel-item .unlimited {
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.yoozmob-carousel-item .unlimited img {
    width: 100%;
    max-width: 600px;
}

.yoozmob-carousel-item .offers {
    flex-basis: 65%;
}

.yoozmob-carousel-item .fox img {
    width: 100%;
    margin-top: 20px;;
}

.yoozmob-carousel-item .downloads {
    width: 100%;
    padding-top: 20px;
    padding-right: 10%;;
}

.yoozmob-carousel-item .downloads img {
    margin: 10px;
    width: 32%;
}

.yoozmob-carousel-item .downloads img:hover {
    cursor: pointer;
    transition: filter 300ms ease;
    filter: brightness(1.2)
}
.yooz-power-bg {
    width: 100%;
    height: 100%;
    overflow: hidden;
    object-fit: cover;
    object-position: right top;
    position: absolute;
    top: 0;
    left: 0;
}
.yoozmob-cta {
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.yoozmob-cta img {
    width: 100%;
}
.downloads-2 {
    max-width: 500px;
    display: flex;
    margin-top: 50px;
}
.downloads-2 a {
    flex: 1;
    padding: 5px;
}
.downloads-2 a img {
    width: 100%;
}
.head-faded {
    display: block;
	background-image: linear-gradient(rgba(255, 255,255,0), rgba(255, 255,255,1), rgba(255, 255,255,1));
    
    margin-top: -100px;
    z-index: 200;
    position: relative;
    height: 200px;
    width: 100%;
}
.fox-2 {
    display: none;
}

@media only screen and (max-width: 768px) {
    .yooz-purple-container {
        align-items: flex-end;
    }
    .yooz-power-container {
        justify-content: flex-end !important;
        padding-bottom: 50px !important;
    }
    .yooz-purple-container .fox{
        display: none;
    }
    .yooz-purple-container .downloads{
        margin: 0;
        padding: 0;
        margin-left: -20px;
        margin-bottom: 30px;
        display: flex;
    }
    .fox-2 {
        display: block;
    }
    .fox-2 img {
        max-width: 400px;
        width: 100%;
    }
    .head-faded {
        height: 60px;
        margin-top: -30px;
    }
    .carousel-indicators {
        margin-bottom: 20px;
    }
    .yoozmob-header {
        max-height: 500px;
        position: relative;
        height: 70vw;
        min-height: auto;
    }
    .yoozmob-header .carousel {
        height: 100%;
    }
    .yoozmob-carousel-item {
        max-height: 500px;
        height: 70vw;
        min-height: auto;
    }
    .yoozmob-carousel-item .downloads a {
        flex: 1;
        padding: 5px;
    }
    .yoozmob-carousel-item .downloads img{
        width: 100%;
    }
    .yoozmob-carousel-item .yooz-power-bg {
        height: 70vw;
    }
}
